.social-share-overlay {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.7);
    top: 0;
    right: 0;
    bottom:0;
    left: 0;
    z-index: 1;
}
.social-link-share-wrapper {
    width: 300px;
    height: 300px;
    position: fixed;
    top: 0;
    right: 0;
    bottom:0;
    left: 0;
    margin: auto;
    border: 1px solid rgb(240, 240, 240);
    border-radius: 8px;
    background: white;
    padding: 20px;
    z-index: 2;
}
.social-wrapper.hidden {
    /* display: none; */
}
.social-wrapper.show {
    /* display: block !important; */
}
.social-link-share-wrapper .title {
    border-bottom: 1px solid lightgrey;
    padding-bottom: 15px;
    text-align: center;
}
.social-link-share-wrapper .share-button-wrapper a {
    display: block;
    color: black;
    padding: 20px;
    font-weight: 600;
    font-size: 17px;
    text-decoration: none;
}
.social-link-share-wrapper .share-button-wrapper a i {
    margin-right: 20px;
}
.social-link-share-wrapper .share-button-wrapper a img {
    width: 16px;
    margin-right: 20px;
}
* {
    padding:0;
    margin: 0;
}
body {
    font-family: sans-serif;
    background-color: #fafafa;
}
.content-wrapper {
    margin-top: 100px;
}
.content-wrapper .post-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    max-width: 625px;
    -webkit-flex-direction: column;
            flex-direction: column;
    border: 1px solid #efefef;
    background-color: #ffffff;
    margin: 0 auto 40px auto;
    padding: 0 0 20px 0;
}
.content-wrapper .post-wrapper .header {
    height: 30px;
    padding: 16px;
    border: 1px solid #efefef;
}
.content-wrapper .post-wrapper .header .profile-pic {
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
}
.content-wrapper .post-wrapper .header .profile-pic img {
    width: 30px;
    height: 30px;
    border-radius: 15px;
}
.content-wrapper .post-wrapper .header .user-info {
    display: inline-block;
    vertical-align: middle;
}
.content-wrapper .post-wrapper .header .user-info .username {
    font-size: 14px;
    font-weight: 600;
    color: #262626;
}
.content-wrapper .post-wrapper .header .user-info .location {
    font-size: 13px;
    margin-top: 4px;
}
.content-wrapper .post-wrapper .post-image img {
    width: 100%;
    height: 400px;
}
.content-wrapper .post-wrapper .social-share-wrapper {
    padding: 20px 16px 0 16px;
}
.content-wrapper .post-wrapper .social-share-wrapper .likes-count {
    padding: 20px 0;
    font-weight: 600;
    font-size: 14px;
}
.content-wrapper .post-wrapper .post-description {
    padding: 0 16px 16px 16px;
}
.content-wrapper .post-wrapper .social-share-wrapper img {
    width: 25px;
    height: auto;
    margin-right: 20px;
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
}
.content-wrapper .post-wrapper .social-share-wrapper .bookmark-icon {
    float: right;
    margin-right: 0;
}
.editor-wrapper {
    position: relative;
}
.editor-wrapper .editor-overlay {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.7);
    top: 0;
    right: 0;
    bottom:0;
    left: 0;
    z-index: 1;
}
.editor-wrapper .fr-box {
    width: 800px;
    height: 300px;
    position: fixed;
    top:55px;
    right:0;
    left: 0;
    margin: auto;
    z-index: 2;
}
.editor-wrapper .btn-wrapper {
    width: 760px;
    height: 18px;
    position: fixed;
    top: 490px;
    right: 0;
    left: 0;
    margin: auto;
    background: white;
    padding: 20px 19px 30px 19px;
    border: 1px solid lightgrey;
    z-index: 2;
}
.editor-wrapper .btn-wrapper #save-btn, .btn-wrapper #close-btn {
    background-color: #3897f0;
    border: 1px solid #3897f0;
    border-radius: 4px;
    color: #fff;
    font-weight: 600;
    margin-right: 10px;
    padding: 7px 15px;
    text-align: center;
    cursor: pointer;
}
.profile-detail-wrapper {
    max-width: 935px;
    margin: 100px auto;
}
.profile-detail-wrapper .user-pic {
    width: 30%;
    display: inline-block;
    vertical-align: middle;
}
.profile-detail-wrapper .user-pic img {
    width: 150px;
    height: 150px;
    border-radius: 75px;
}
.profile-detail-wrapper .user-info {
    width: 48%;
    display: inline-block;
    vertical-align: middle;
}
.profile-detail-wrapper .user-info .details>div {
    display: inline-block;
    vertical-align: middle;
    margin-right: 15px;
}
.profile-detail-wrapper .user-info .details .username {
    font-size: 28px;
    line-height: 32px;
    display: inline-block;
}
.profile-detail-wrapper .user-info .details .upload-btn {
    background-color: transparent;
    border: 1px solid #dbdbdb;
    border-radius: 3px;
    color: #262626;
    padding: 5px 10px;
    font-weight: 500;
    cursor: pointer;
}
.profile-detail-wrapper .user-info .details .settings img{
    width: 30px;
    height: 30px;
    cursor: pointer;
}
.profile-detail-wrapper .user-info .social-share-wrapper {
    margin: 20px 0;
}
.profile-detail-wrapper .user-info .social-share-wrapper>div{
    margin-right: 20px;
    display: inline-block;
    vertical-align: middle;
}
.profile-detail-wrapper .user-info .social-share-wrapper>div span:nth-child(1){
    font-weight: 600;
    margin-right: 10px;
}
.profile-detail-wrapper .user-info .social-share-wrapper>div span:nth-child(2){
    font-weight: normal;
}
.profile-detail-wrapper .nav-tabs {
    border-top: 1px solid #efefef;
    -webkit-align-items: center;
            align-items: center;
    color: #999;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    font-size: 12px;
    font-weight: 600;
    -webkit-justify-content: center;
            justify-content: center;
    letter-spacing: 1px;
    text-align: center;
    margin: 50px 0;
}
.profile-detail-wrapper .nav-tabs a{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    height: 52px;
    text-transform: uppercase;
    margin-right: 60px;
    text-decoration: none;
    color: #999;
}
.profile-detail-wrapper .nav-tabs a.active {
    border-top: 1px solid #262626;
}
.profile-detail-wrapper .nav-tabs a span {
    padding: 3px 0;
}
.profile-detail-wrapper .post-image {
    width: 33%;
    display: inline-block;
    margin-bottom: 20px;
}
.profile-detail-wrapper .post-image img {
    width: 293px;
    height: 293px;
}
.header-wrapper {
    position: fixed;
    height: 50px;
    width: 100%;
    top: 0;
    border-bottom: 1px solid lightgrey;
    background-color: #ffffff;
}
.header-wrapper .inline-wrapper {
    width: 30%;
    display: inline-block;
    padding: 10px 20px;
    text-align: center;
    vertical-align: middle;
}
.header-wrapper .logo-wrapper img {
    display: inline-block;
    vertical-align: middle;
}
.header-wrapper .logo-wrapper img:nth-child(1){
    width: 24px;
    height: 24px;
    margin-right: 10px;
}
.header-wrapper .logo-wrapper img:nth-child(2){
    width: 103px;
    height: 29px;
}
.header-wrapper .search-wrapper form {
    position: relative;
}
.header-wrapper .search-wrapper form input[type="submit"]{
    display: none;
}
.header-wrapper .search-wrapper form .tag-suggestion-wrapper {
    background: #fff;
    /* border: 1px solid #f7f7f7; */
    border-radius: 3px;
    display: block;
    left: 50%;
    margin-left: -121px;
    position: absolute;
    right: -12px;
    top: 45px;
    width: 243px;
    max-height: 400px;
    overflow-y: scroll;
    z-index: 9;
}
.header-wrapper .search-wrapper form .tag-suggestion-wrapper a.selected {
    background-color: #efefef;
}
.header-wrapper .search-wrapper form .tag-suggestion-wrapper a {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    text-decoration: none;
    color: #003569;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: solid 1px #efefef;
    height: 50px;
    padding: 8px 14px;
}
.header-wrapper .search-wrapper form .tag-suggestion-wrapper a:hover {
    background-color: lightgray;
}
.header-wrapper .search-wrapper form .tag-suggestion-wrapper div:nth-child(1) {
    display: inline-block;
    vertical-align: middle;
    color: lightgrey;
    font-size: 25px;
}
.header-wrapper .search-wrapper form .tag-suggestion-wrapper div:nth-child(2) {
    display: inline-block;
    vertical-align: middle;
    margin-left: 15px;
    text-align: left;
    font-size: 14px;
}
.header-wrapper .search-wrapper form .tag-suggestion-wrapper div:nth-child(2) span:nth-child(1){
    color: #262626;
    font-weight: 600;
    display: block;
}
.header-wrapper .search-wrapper form .tag-suggestion-wrapper div:nth-child(2) span:nth-child(2){
    color: #999;
    display: block;
}
.header-wrapper .search-wrapper input{
    width: 215px;
    height: 17px;
    border: 1px solid lightgrey;
    background-color: #f7f7f7;
    padding: 5px;
}
.header-wrapper .profile-info-wrapper img {
    width: 24px;
    height: 24px;
    margin-right: 45px;
    cursor: pointer;
}
